<template>
  <div class="container bg-container">
    <Header :title="t('index.添加流動性')" type="back"></Header>
    <div class="lp">
      <div class="border-out">
        <div class="border-in">
          <div class="card">
            <div class="title">{{t('index.您的身份')}} <span>{{t('index.無效協議')}}</span></div>
            <van-cell-group inset class="input-wrap">
              <div class="key">{{t('index.您当前的LP值')}}</div>
              <van-field readonly type="number" v-model="lpnum" clearable />
            </van-cell-group>
            <!-- <van-cell-group inset class="input-wrap">
          <div class="key">{{t('index.您在LP池中擁有的trx數量')}}</div>
          <van-field readonly type="number" v-model="numTrx" clearable />
        </van-cell-group> -->
            <div class="tip">{{t('index.成為有效協議的條件：LP≥key',{key:wherelp})}}</div>
            <div class="btn-wrap" style="margin:18px 0 7px;">
              <van-button @click="toLink" type="primary">{{t("index.前往SUNSWAP添加流動性")}}</van-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLp } from "@/api"
import config from "@/utils/config"
export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts),
      web3: computed(() => store.state.provider),
      balance: 0,
      lpnum: '0.00',
      wherelp: '0.00',
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.getLp()
      },
      toLink: () => {
        window.location.href = `https://sunswap.com/#/v2?t0=${config.usdtAddress}&t1=${config.oenAddress}&type=swap&from=scan`
      },
      getLp: () => {
        getLp({ address: data.address }).then((res) => {
          data.lpnum = res.data.lpnum
          data.wherelp = res.data.wherelp
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.lp {
  padding: 12px 15px;
  .card {
    padding: 20px;
    color: #9b9a9a;
    .title {
      font-size: 15px;
      padding-bottom: 14px;
      span {
        font-size: 20px;
        color: #1ba27a;
        font-weight: 600;
      }
    }
    .input-wrap {
      .key {
        padding: 18px 0 10px;
        font-size: 14px;
      }
    }
    .tip {
      padding: 10px 0;
      font-size: 12px;
      color: rgba(255, 255, 255, 0.7);
    }
  }
}
</style>
<style lang="less">
